import { Animator } from "./animator";
import { CanvasBuilder } from "./canvasBuilder";
import { XmlManager } from "@/utils/helpers";

class Orbital {
    constructor(container , framesCount , speed){
        
        this.xml;

        this.container = container;
        this.framesCount = framesCount;
        this.speed = speed;

        this.animationsPaths = [];

        this.animator, this.resizer , this.canvasBuilder;

    }

    init(){

        this.xml = new XmlManager("config.xml");

        this.xml.loadXmlData(() => {
            this.htFromXml = this.xml.getHotspots();
            this._setup();
        });
    }

    _setup() {

        const levelsCount = 4; // must refactor

        for (let i = 0; i < levelsCount; i++) {
            const levelPath = `assets/tiles/l${i+1}/resized/0${i}_?.jpg`;
            this.animationsPaths.push(levelPath);
        }

        this._setConfigStyles();

        this.canvasBuilder = new CanvasBuilder(this.container);
        this.canvasBuilder.init((canvasGroup) => {
            this.canvasGroup = canvasGroup;
        });


        this.animator = new Animator(
            this.canvasGroup,
            this.animationsPaths ,
            this.framesCount ,
            this.speed,
            1920,
            1080,
            this.htFromXml
        );

        

        window.addEventListener("OnAnimatorReady" , () => this.onAppReady() , {once : true});

    }

    _setConfigStyles(){
        document.body.style.touchAction = "none";
        document.getElementById(this.container).style.touchAction = "none";
    }

    onAppReady() {


        this.animator.run();

        window.dispatchEvent(new CustomEvent("OnAppReady", {
            bubbles: false
        }));
    }
    

    playOrbital(){
        this.animator.play();
    }

    stopOrbital(){
        this.animator.stop();
    }

    next(){
        this.animator.playFrame(false);
    }

    prev(){
        this.animator.playFrame(true);
    }

    setLevel(level){
        this.animator.switchAnimation(level);
    }
    
    filterHSByPrice(surface) {
        this.animator.stop();
        this.animator.hotspots.setSurfaceFilter(surface);
    }
    

    setZoomOptions (opt){
        this.animator._inputSystem.zoom.setOptions(opt);
    } 
    
}

export {Orbital}